import React from 'react';
//Simport './App.css';
import { Route, Switch } from 'react-router-dom'
import HomePage from './pages/HomePage'
import ContactPage from './pages/ContactPage'
import ServicePage from './pages/ServicePage'
import ServicesPage from './pages/ServicesPage'
import ReviewPage from './pages/ReviewPage'
import DoctorPage from './pages/DoctorPage'

export default function App() {
  return (
    <Switch>
      <Route exact path="/"   component={() => <HomePage/>} />
      <Route path="/contact"  component={ContactPage} />
      <Route exact path="/services" component={ServicesPage} />
      <Route path="/review"   component={ReviewPage} />
      <Route path="/meet"     component={DoctorPage} />
      <Route path="/services/aortic-aneurysm" component={() =><ServicePage id = "0"/>} />
      <Route path="/services/carotid-artery-disease" component={() =><ServicePage id = "1"/>} />
      <Route path="/services/deep-vein-thrombosis" component={() =><ServicePage id = "2"/>} />
      <Route path="/services/dialysis-access-management" component={() =><ServicePage id = "3"/>} />
      <Route path="/services/leg-cramps" component={() =><ServicePage id = "4"/>} />
      <Route path="/services/leg-swelling" component={() =><ServicePage id = "5"/>} />
      <Route path="/services/mesenteric-disease" component={() =><ServicePage id = "6"/>} />
      <Route path="/services/renal-artery-stenosis" component={() =><ServicePage id = "7"/>} />
    </Switch>
  )}
