import React from 'react'
import { Link } from 'react-router-dom'
import {Button,Card} from 'react-bootstrap'
export default function ServiceCard(props){
  const link = "/services/"+props.link
  return(
    <Card bg = "light" >
      <Card.Img
        height = "160px"
        width = "160px"
        variant="top"
        src={props.img} />
        <Card.Body>
          <Card.Title>{props.title}</Card.Title>
          <Card.Text>
            {props.text}
          </Card.Text>
          <Link className = 'align-bottom' to = {link}><Button variant="secondary" >Learn More</Button></Link>
        </Card.Body>
      </Card>
  )
}
