import React from 'react'
import Header from '../components/Header'
import Footer from '../components/Footer'
import { Row, Col, Container,Jumbotron, Form, Button } from 'react-bootstrap'
export default function ReviewPage() {
  return (
    <Container fluid = {true} className="container">
    <Header />
    <Jumbotron className = "Lead-text">
      <h1>Tell us about your visit.</h1>
      <p>
        If you enjoyed your service, or if there is something that we could
        improve on, let us know below. We appreciate all feedback.
      </p>
    </Jumbotron>
    <Form>
      <Row className = "justify-content-md-center">
          <Col md  lg="3">
            <Form.Label>First Name</Form.Label>
            <Form.Control type="name" placeholder="First Name" />
          </Col>
          <Col md  lg="3">
            <Form.Label>Last Name</Form.Label>
            <Form.Control type="name" placeholder="Last Name" />
          </Col>
      </Row>
    <br />
      <Row className = "justify-content-md-center">
        <Form.Group controlId="formBasicEmail">
          <Form.Label>Email address</Form.Label>
          <Form.Control type="email" placeholder="Enter email" />
          <Form.Text className="text-muted">
          We'll never share your email with anyone else.
          </Form.Text>
        </Form.Group>
      </Row>
      <Row className = "justify-content-md-center">
        <Col md lg = "4">
          <Form.Group controlId="exampleForm.ControlSelect2">
            <Form.Label>How would you rate your visit? (l lowest to 5 highest)</Form.Label>
            <Form.Control as="select">
              <option>5</option>
              <option>4</option>
              <option>3</option>
              <option>2</option>
              <option>1</option>
            </Form.Control>
          </Form.Group>
        </Col>
      </Row>
      <Row className = "justify-content-md-center">
        <Col md lg ="5">
        <Form.Group controlId="exampleForm.ControlTextarea1">
          <Form.Label>Comments</Form.Label>
          <Form.Control as="textarea"
                        rows="4"
                        placeholder="Any feedback is appreciated."/>
        </Form.Group>
        </Col>
      </Row>
      <Row className = "justify-content-md-center">
        <Button>Submit
        </Button>
      </Row>
    <br />
    </Form>
    <Footer />
    </Container>
  )
}
