import React from "react"
import '../App.css';
import { Row, Col } from 'react-bootstrap'
import logo from '../assets/Logo.jpg'
import { Link } from 'react-router-dom'
function Footer(){
  return(
    <div className = "Foot">
      <br />
      <Row>
        <Col><h4>Who We Are</h4>
        <p>
          <img
            src = {logo}
            width="50"
            className = "floatLeft"
            height = "50"
            alt='Home'
          />
        </p>
        <p>MVV will provide vascular surgery care for the adult and elderly
           population as well as providing multiple procedures to create a
            vascular care solution. </p>
        </Col>
        <Col><h4>Quick Access</h4>
          <Link to = '/'          className = 'Footer-Link'>Home <br /></Link>
          <Link to = '/meet'      className = 'Footer-Link'>The Doctor <br /></Link>
          <Link to = '/services'  className = 'Footer-Link'>Services<br /> </Link>
          <Link to = '/contact'   className = 'Footer-Link'>Contact Us<br />  </Link>
          <Link to = '/review'    className = 'Footer-Link'>Review </Link>
      </Col>
        <Col><h4>Contact Us</h4>
          <p>
            milnervein@gmail.com<br />
            <a href="tel:678-580-1149">678-580-1149</a> Phone
          </p>
        </Col>
        <Col><h4>Come Visit</h4>
          <p>
            770-557-1347 Fax<br />
            5700 Hillandale Dr<br />
            Lithonia, Dekalb County 30058 Ste. 120
          </p>
        </Col>
      </Row>
      <Row className = "signature">
        <p>This website designed by Matthew Mahoney (mahoney.a.matthew@gmail.com)</p>
      </Row>
    </div>
  )
}
export default Footer
