import React from 'react'
import {Row,CardDeck } from 'react-bootstrap'
import AoAn from '../assets/AoAn.jpg'
import CAS from '../assets/cas.jpg'
import ServiceCard from '../components/ServiceCard'
export default function ServiceCards(){
  return(
    <div>
    <Row>
      <CardDeck>
        <ServiceCard
          img = {AoAn}
          title = 'Aortic Aneurysm'
          text = 'This condition is an enlargement of the aorta, the main blood
                  vessel that deliver blood to the body, at the level of the
                  abdomen.'
          link = 'aortic-aneurysm'
          />
          <ServiceCard
            img={CAS}
            title = 'Carotid Artery Disease'
            text= 'Narrowing of the blood vessels in the neck that carries blood
                   from the heart to the brain.'
            link = 'carotid-artery-disease'
          />
          <ServiceCard
            img ={AoAn}
            title = 'Deep Vein Thrombosis'
            text = 'A blood clot in deep vein, usually in the legs.'
            link = 'deep-vein-thrombosis'
          />
          <ServiceCard
            img = {AoAn}
            title='Dialysis Access Managment'
            text='Dialysis is the clinical purification of blood by dialysis,
                  as a substitute for the normal dfunction of the kidney.'
            link = 'dialysis-access-management'
            />
        </CardDeck>
        <br />
      </Row>
      <Row>
        <CardDeck>
          <ServiceCard
            img={AoAn}
            title='Leg Cramps/ Peripheral Artery'
            text=' A circulatory condition in which narrowed blood vessels
               reduce blood flow to the limbs.'
            link = 'leg-cramps'
            />
          <ServiceCard
            img={CAS}
            title='Leg Swelling'
            text='Improper functioning of the vein valves in the leg,
                  causing swelling and skin changes.'
            link='leg-swelling'
            />
          <ServiceCard
            img={AoAn}
            title='Mesenteric Disease'
            text='This condition is known in which injury of the small
                  intestine occurs due to not enough blood supply.'
            link='mesenteric-disease'
            />
          <ServiceCard
            img={AoAn}
            title='Renal Artery Stenosis'
            text='A narrowing of arteries that carry blood to one or both of the kidneys.'
            link='renal-artery-stenosis'
            />
        </CardDeck>
      </Row>
    </div>
)}
