import React from 'react'
import { Link } from 'react-router-dom'
import {Accordion, Card, Button,} from 'react-bootstrap'
export default function AccordionCard(props){
  const link = "/services/"+props.link
  return(
    <Card>
      <Card.Header>
        <Accordion.Toggle as={Button} variant="link" eventKey={props.num}>

          {props.name}
        </Accordion.Toggle>
      </Card.Header>
      <Accordion.Collapse eventKey={props.num}>
        <Card.Body>{props.description} <br />
          <Link to = {link}><Button>Learn More</Button></Link>
        </Card.Body>
      </Accordion.Collapse>
    </Card>
    )}
