import React from 'react'
import Header from '../components/Header'
import Footer from '../components/Footer'
import {Container} from 'react-bootstrap'
import services from '../services.json'
export default function ServicePage(props){
  const service = services.services.find(item => item.id === props.id)
  if(service === undefined){
    return(<div>
    Not implemented yet</div>)
  }
  return(
    <Container fluid = {true} className="container">
      <Header />
      <h3 className = "indent">     {service.name} </h3>
      <p>{service.description}</p>
      <hr />
        <div className = "center-text">
          <h4>Common Causes</h4>
          <p>
            Not sure if you are suffering from {service.name.toLowerCase()}?, it is more likely
            to develop if you have suffered any of the following...
          </p>
          <hr />
        </div>
      <div className = "center-text">
        <h4>Symptoms</h4>
        <p>if you are experiencing any of the following, it may be time
           to consider seeing a professional.
        </p>
      </div>
      <hr />
      <div className = "center-text">
        <h4>Treatments</h4>
        <p>
          We offer the following in top-class methods to help you quickly get
          back to feeling 100%.
        </p>
    </div>
    <Footer />
    </Container>

  )
}
