import React from 'react'
import Header from '../components/Header'
import Footer from '../components/Footer'
import {Container,Accordion, Card, Button, Jumbotron } from 'react-bootstrap'
import AccordionCard from '../components/AccordionCard'
export default function ServicesPage() {
  return (
    <Container fluid = {true} className="container">
    <Header />
    <Jumbotron className = "Lead-text">
      <h1>Services</h1>
      <p>
        We provide a number of treatments and services to suit the needs
        of our patients.
      </p>
      <p>
        See below for your treatment options.
      </p>
    </Jumbotron>
  <Accordion>
    <AccordionCard
      name = "Aortic Aneurysm"
      description = "A Comprehensive Approach
        This condition Is and enlargement of the aorta,
        the main blood vessel that deliver blood to the body,
        at the level of the abdomen."
      link = "aortic-aneurysm"
      num = '0'
      />
    <AccordionCard
      name = 'Carotid Artery Disease'
      description = 'Narrowing of the blood vessels in the neck that carries
                     blood from the heart to the brain.'
      link = "carotid-artery-disease"
      num = '1'
     />
    <AccordionCard
      name = 'Deep Vein Thrombosis'
      description = 'A blood clot in deep vein, usually in the legs.'
      link = "deep-vein-thrombosis"
      num = '2'
     />
   <AccordionCard
      name='Dialysis Access Management'
      description='Dialysis is the clinical purification of blood by dialysis, as a substitute for the normal dfunction of the kidney.'
      link = "dialysis-access-management"
      num = '3'
      />
    <Card>
      <Card.Header>
        <Accordion.Toggle as={Button} variant="link" eventKey="4">
          Leg Cramps/ Peripheral Artery
        </Accordion.Toggle>
      </Card.Header>
      <Accordion.Collapse eventKey="4">
        <Card.Body> A circulatory condition in which narrowed blood vessels reduce blood flow to the limbs.</Card.Body>
      </Accordion.Collapse>
    </Card>
    <Card>
      <Card.Header>
        <Accordion.Toggle as={Button} variant="link" eventKey="5">
          Leg Swelling
        </Accordion.Toggle>
      </Card.Header>
      <Accordion.Collapse eventKey="5">
        <Card.Body> Improper functioning of the vein valves in the leg, causing swelling and skin changes.</Card.Body>
      </Accordion.Collapse>
    </Card>
  </Accordion>
      <Footer />
    </Container>
  )
}
