import React from "react"
import Header from '../components/Header'
import Footer from '../components/Footer'
import {Container, Jumbotron } from 'react-bootstrap'
import Map from "../components/Map"
function Contact(){
  return(
    <Container fluid = {true} className="container">
      <Header />
        <Jumbotron className = "Lead-text">
          <h1>Get in touch</h1>
            <p>
              If you feel it is the appropriate time to see the doctor, get in contact
              and let's schedule and appointment.
            </p>
            <p>
              Give us a call at <a href="tel:678-580-1149">678-580-1149</a> or
              send an email to <b>milnervain@gmail.com</b> and
              we can schedule an appointment.
            </p>
            <br />
            <p></p>
          </Jumbotron>
          <Map />
        <Footer />
    </Container>
    )}

export default Contact
