import React from 'react'
import Header from '../components/Header'
import Footer from '../components/Footer'
import {Container} from 'react-bootstrap'
import ServiceCards from "../components/ServiceCards"
import HomeCarousel from "../components/HomeCarousel"
import DocInfo from "../components/DocInfo"
import Map from "../components/Map"
export default function HomePage() {

  return (
    <Container fluid = {true} className="container">
    <Header />
    <HomeCarousel />
    <br />
    <div >
    <h4 className = "center-text">What we offer</h4>
    </div>
    <hr width = "30%"/>
    <ServiceCards />
    <hr />
    <h3 className = "center-text">Meet the Doctor</h3>
    <hr width = "30%"/>
    <DocInfo />
    <hr width = "30%"/>
    <h3 className = "center-text">Contact Us</h3>
    <p className = "center-text">
      Call us at <a href="tel:678-580-1149">678-580-1149</a>
    , Email us at milnervein@gmail.com, or come by today.
    </p>
    <hr width = "60%"/>

    <Map />
            <Footer />
  </Container>
  )
}
