import React from "react"
import Header from '../components/Header'
import Footer from '../components/Footer'
import {Container, Carousel, Image} from 'react-bootstrap'
import DocInfo from "../components/DocInfo"
import DocArms from "../assets/doc2.jpg"
import docImage from "../assets/smallerdoc.jpg"
export default function Doctor(){
  return(
    <Container fluid = {true} className="container">
      <Header />
      <Carousel
        controls = {false}
        indicators = {false}
        >
        <Carousel.Item >
          <img
            className="d-block mw-100"
            src={DocArms}
            alt="First slide"
          />
        <Carousel.Caption>
          <p className = "large-text">About Dr. Timothy Milner, MD, FACS</p>
          <br /><br /><br /><br />
          </Carousel.Caption>
        </Carousel.Item>
        </Carousel>
        <div style = {{"paddingLeft": "20%", "fontSize":"1.5em"}}>
          <Image src = {docImage} rounded/>
          <h3><b>Dr.Timothy Milner</b></h3>
          <p>
            <b>Hometown:</b> INSERT HOMETOWN<br /><br />
            <b>Undergraduate Education:</b> INSERT EDUCATION<br /><br />
            <b>Graduate Education:</b> INSERT EDUCATION<br /><br />
            <b>Medical Education:</b> INSERT EDUCATION<br /><br />
            <b>Surgical Training:</b> INSERT EDUCATION<br /><br />
            <b>Vascular Surgery Training:</b>INSERT EDUCATION<br /><br />
            <b>Board Certified:</b>INSERT EDUCATION<br /><br />
            <b>Memberships:</b>INSERT EDUCATION<br /><br />
            <b>In Practice Since:</b> 1738<br /><br />
            <b>Accepting New Patients:</b> Yes<br /><br />
            <b>Special Interests:</b> INSERT EDUCATION<br /><br />
          </p>
        </div>
      <Footer />
    </Container>
  )}
