import React from 'react'
import { Link } from 'react-router-dom'
import { Carousel, Button } from 'react-bootstrap'
import firstImage from "../assets/1.jpg"
import secondImage from "../assets/2.jpg"
import thirdImage from "../assets/3.jpg"
export default function HomeCarousel(){
    return(
      <Carousel >
        <Carousel.Item >
          <img
            className="d-block mw-100"
            src={firstImage}
            alt="First slide"
          />
        <Carousel.Caption>
          <p className = "large-text">The Best Care Available</p>
          <p className = "small-text">Let us take care of you</p>
          <br /><br />
            <Link to = '/services'><Button variant="light">Services</Button></Link>
          </Carousel.Caption>
        </Carousel.Item>
        <Carousel.Item>
          <img
            className="d-block w-100 mh-25"
            src={secondImage}
            alt="Second slide"
          />
          <Carousel.Caption>
            <p className = "large-text">Make an Appointment</p>
            <p className = "small-text">Get in touch so you can get the care you need</p>
            <br /> <br />
            <Link to = '/contact'><Button variant = 'light'>Contact Us</Button></Link>
          </Carousel.Caption>
        </Carousel.Item>
        <Carousel.Item>
          <img
            className="d-block w-100 mh-25"
            max-width = "100%"
            height = "auto"
            src={thirdImage}
            alt="Third slide"
            />
          <Carousel.Caption>
            <text className = "large-text">Read or Leave a Review</text>
            <p className = "small-text">See what others had to say.</p>
            <br /> <br />
     <Link to = '/review'><Button variant = 'light'>Review</Button></Link>
    </Carousel.Caption>
  </Carousel.Item>
</Carousel>
  )}
